AM;EACN,OAAO;EACP,QAAQ;EACR,SAAS;EACT,sBAAsB;EACtB,uBAAuB;EACvB,eAAe;AACjB;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,WAAW;EACX,SAAS;EACT,WAAW;EACX,uBAAuB;EACvB,uBAAuB;EACvB,eAAe;AACjB;AAEA;EACE,yBAAyB;AAC3B;AAEA;EACE,2BAA2B;AAC7B;AAEA;EACE,mCAAmC;EACnC,+BAA+B;EAC/B,2BAA2B;AAC7B;AAEA;EACE,mBAAmB;AACrB;AAEA;EACE,kBAAkB;AACpB;AACA;IACI,aAAa;AACjB;AACA;IACI,mBAAmB;AACvB;AACA;EACE,mBAAmB;AACrB;AAEA;IACI,oCAAoC;IACpC,gCAAgC;AACpC;AAEA;IACI,2BAA2B;AAC/B;AAEA;IACI,aAAa;AACjB;AAEA;IACI,yBAAyB;IACzB,WAAW;IACX,WAAW;AACf;AAEA;IACI,2BAA2B;AAC/B;AACA;KACK,mCAAmC;KACnC,eAAe;AACpB;AACA;IACI,kBAAkB;AACtB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed } from '@vue/reactivity';\nimport { defineAsyncComponent } from 'vue';\nimport { container } from '../../../../domain/core/diContainer';\nimport { IHeaderButton } from '../../../../domain/model/ui/IHeaderButton';\nimport { IDialog } from '../../../../domain/presentation/IDialog';\nimport { INotification } from '../../../../domain/presentation/INotification';\nimport { IStudio } from '../../../../domain/useCase/IStudio';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport 'bootstrap/js/dist/dropdown'\nimport { IApplication, IExportViewItem } from '../../../../domain/model/models';\nimport Tabs from \"../../../vue3/components/ui/tabs.vue\";\nimport { ITab, ITabs } from '../../../vue3/components/ui/ITabs'\nimport { ref } from 'vue';\nimport { IGetExportSelectionData } from '../../../../domain/useCase/IGetExportSelectionData';\nimport { IRouter } from '../../../../domain/presentation/IRouter';\nimport ExportApp from \"../../components/application/exportApp.vue\";\nimport { ILocalization } from '../../../../domain/infrastructure/ILocalization';\nimport { ExportType } from '@stechquick/algae/lib/qCloudTemp/exporter';\nimport { ILogout } from '../../../../domain/useCase/ILogout';\n\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst selectionData = ref([] as unknown as Array<IExportViewItem>);\nconst router = container.resolve<IRouter>(IRouter);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst checkedOut = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst props = defineProps<{ app: IApplication }>();\nconst deployTip = ref(\"\");\nconst hoverValue = ref(false);\n\nconst tablist = computed(() => {\n  return viewModel.studio?.openItems?.map(item => {\n    const retVal: ITab = {\n      name: item.name,\n      type:item.modelType,\n      id: item.ID,\n      selected: viewModel.studio.currentItem?.ID == item.ID,\n      modified: item.modified?.model,\n      synced: item.synced,\n      cb: {\n        close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n        select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n      }\n    };\n    return ref(retVal).value;\n  }) || [];\n});\n\nfunction onCheckout() {\n  const currentItem = viewModel.studio.currentItem;\n  if (!currentItem) { return; }\n\n  executor.execute(async () => studio.checkout(currentItem), { loading: true });\n}\nfunction onSave() {\n  const currentItem = viewModel.studio.currentItem;\n  if (!currentItem) { return; }\n\n  executor.execute(async () => studio.save(currentItem), { loading: true });\n}\nfunction onSavePlus() {\n  dialog.showDialog(defineAsyncComponent(() => import(\"../dialogs/studio/savePlus.vue\")), { closable: true });\n}\nfunction onItemSelect(selectedItem: IHeaderButton) {\n  selectedItem.onSelect();\n}\n\nasync function release() {\n  executor.execute(async () => studio.release(viewModel.studio.currentItem!), { loading: false });\n}\nfunction listExportjobs() { \n    // router.push(\"/main/exportJobList\");\n    dialog.showDialog(defineAsyncComponent(() => import(`../../../vue3/pages/main/exportJob.vue`)), { closable: true, title: \"Export Job List\" ,width:'990px',height:'500px', overflow: 'auto'});\n}\nasync function openExportDialog(exportType:ExportType){\n  executor.execute(async () => {\n    const applicationID = routerParams.appID;\n    selectionData.value =await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n    // dialog.showDialog(defineAsyncComponent(() => import(`../../components/application/exportApp.vue`)), { closable: true, title: viewModel.studio.appName, width:'auto',height:'auto',overflow:'auto' }, {data:selectionData} );\n    dialog.showDialog(ExportApp, { closable: true, title: 'Export-'+viewModel.studio.appName, image:{src: props.app?.logo || '../../../../images/is-bankasi-logo.jpg',width:'50px',height:'50px'}, width:'1000px',height:'750px',overflow:'unset' }, {selectionData: selectionData.value, exportType} );\n      \n  }, { loading: true });\n\n}\nfunction goMainPage(){\n    router.push(\"/main/applicationList\");\n}\n\nfunction checkDeployConditions() {\n    return !viewModel.apps.find((app) => app.ID === viewModel.studio.appId)?.deployParameters?.gitProductRepoId;\n}\n\nfunction logout() {\n  executor.execute(async () => {\n    await executor.executeUseCase<ILogout>(ILogout);\n    router.push(\"/login\");\n  }, { loading: true });\n}\n\nfunction showDeployTip() {\n    if (checkDeployConditions()) {\n        deployTip.value = localization.get(\"deployTip\");\n    } else {\n        deployTip.value = \"\"\n    }\n}\n\nfunction openDeployScreen() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../dialogs/deploy/deployScreen.vue`)), { closable: true, title: \"Deploy\", width: \"1417px\", height: \"741px\" })\n}\n\nfunction hoverLogo(){\n    hoverValue.value=true\n}\nfunction unHover(){\n    hoverValue.value=false\n}\n</script>\n<template>\n  <div  class=\"top-menu\">\n    <div class=\"row top-container\">\n      <div class=\"col-lg-1 home-section\" style='display: flex;'>\n        <div class=\"corner-container\">\n          <div class=\"logo-container\" @click=\"goMainPage\" @mouseover=\"hoverLogo()\" @mouseleave=\"unHover()\" :style=\"{ 'background-color':hoverValue? '#151233':'', 'padding':hoverValue? '18px':'12px'  }\" >\n            <img v-if=\"!hoverValue\"  src=\"../../../../images/ide5-logo.PNG\" alt=\"ide5-logo\" width=\"35\" height=\"35\" >\n            <img v-if=\"hoverValue\"    src=\"../../../../images/plateau_hover.svg\" alt=\"ide5-logo\"  width=\"24\" height=\"24\" >\n          </div>\n        </div>\n            <div class=\"vl ms-3\"></div>\n      </div>\n\n      <div class=\"col-lg-8 middle-resize\">\n        <div class=\"middle-container\">\n          <div class=\"title-container\">\n            <img src=\"../../../../images/is-bankasi-logo.jpg\"  alt=\"is-bankasi-logo\" width=\"24\" height=\"24\"\n              class=\"title-logo\">\n            <p class=\"title\">{{ viewModel.studio.appName }}</p>\n          </div>\n          <div class=\"tabs\">\n            <Tabs :tabs=\"tablist\" height=\"30px\" ref=\"tabComp\"></Tabs>\n          </div>\n        </div>\n      </div>\n      <div class=\"col-lg-3 last-resize\">\n        <div class=\"end-container position-absolute top-0 end-0\">\n          <button type=\"button\" class=\"btn-sm end-icons export-button\" id=\"dropdownMenuButton1\"\n            data-bs-toggle=\"dropdown\" aria-expanded=\"false\"><i class=\"mdi mdi-export-variant export\"></i><span\n              class=\"export-text\">Export</span></button>\n          <ul  class=\"dropdown-menu menu-export\" aria-labelledby=\"dropdownMenuButton1\">\n                        <li><a class=\"dropdown-item export-menu-items\" href=\"#\" @click=\"openExportDialog('qui')\"><span class=\"export-menu-text\