iewModelManager } from \"../../../../domain/viewModel/IViewModelManager\";\nimport { computed, ref } from 'vue';\nimport { ITreeView, ITreeViewInstances } from '../../../../domain/presentation/ITreeView';\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IFileExplorer } from \"../../../../domain/useCase/IFileExplorer\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst sidebarSectionElement = ref<HTMLSelectElement>();\nconst ui = viewModel.studio.ui;\nconst studio = container.resolve<IStudio>(IStudio);\nconst sidebarManager = container.resolve<ISidebarManager>(ISidebarManager);\nconst item = { \"name\": \"files\", \"sidebarComponent\": \"explorer.vue\", \"selected\": true }\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst navigationItems =viewModel!.studio!.ui?.navItems\n\nnavigationItems?.forEach(item=>{\n  onNavItemSelect(item)\n})\n\n\nlet initialMenu = document.getElementsByClassName(\"vertical-nav-studio\")[0];\nlet expandedMenu = document.getElementsByClassName(\"vertical-nav-expanded\")[0];\nlet closeMenuIcon = document.getElementsByClassName(\"chevron-left\")[0];\nlet expandMenuIcon = document.getElementsByClassName(\"menu-icon\")[0];\nfunction onNavItemSelect(item: INavItem) {\n  if (item.selected && item.sidebarComponent) {\n    studio.nav_unselect();\n    sidebarManager.hide();\n    return;\n  }\n  if (item.sidebarComponent) {\n    sidebarManager.show(item.sidebarComponent);\n  } else {\n    sidebarManager.hide();\n  }\n  studio.nav_selectItem(item);\n}\nfunction openMenu() {\n  (<any>document.getElementsByClassName(\"vertical-nav-studio\")[0]).style = \"display: none\";\n  (<any>document.getElementsByClassName(\"vertical-nav-expanded\")[0]).style = \"display: block\";\n}\nfunction closeMenu() {\n  (<any>document.getElementsByClassName(\"vertical-nav-expanded\")[0]).style = \"display: none\";\n  (<any>document.getElementsByClassName(\"vertical-nav-studio\")[0]).style = \"display: block\";\n}\nonMounted(() => {\n  (<any>document.getElementsByClassName(\"vertical-nav-expanded\")[0]).style = \"display: none\";\n  document.getElementsByClassName(\"menu-icon\")[0].addEventListener('click', openMenu)\n  document.getElementsByClassName(\"chevron-left\")[0].addEventListener('click', closeMenu)\n  openMenu()\n\n});\nfunction moveHandler(event: MouseEvent) {\n  if (!ui) {\n    return;\n  }\n  ui.sidebar.left = (event.x - sidebarSectionElement.value!.offsetLeft - 2);\n}\nfunction mousedown(event: MouseEvent) {\n  window.addEventListener(\"mousemove\", moveHandler);\n  event.preventDefault();\n}\nfunction mouseup() {\n  window.removeEventListener(\"mousemove\", moveHandler);\n}\nasync function collapse() {\n  const treeview = await executor.resolveUseCase<ITreeView>(ITreeViewInstances.FileExplorer);\n  treeview.collapseAll()\n\n}\nasync function addModule() {\n  const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n  fileExplorer.onNewItemSelect(viewModel.studio.appId,viewModel.studio.appName,  'application', 'Module')\n}\nasync function addScreen() {\n  const fileExplorer = await executor.resolveUseCase<IFileExplorer>(IFileExplorer);\n  fileExplorer.onNewItemSelect(viewModel.studio.appId,viewModel.studio.appName,  'application', 'Screen')\n}\n\nasync function attachModuleToApp() {\n  dialog.showDialog(defineAsyncComponent(() => import(`../dialogs/attachModuleToApp.vue`)), {\n    closable: true,\n    title: \"Add Existing Module\",\n    width: \"1000px\",\n    closeOnOutClick: true,\n  });\n}\n\nconst leftpx = computed(() => {\n  if (!ui) {\n    return undefined;\n  }\n  return ui.sidebar.left + \"px}\";\n})\n\nfunction openSettings() {\n    dialog.showDialog(defineAsyncComponent(() => import(`../dialogs/deploySettings.vue`)), { closable: true, title: \"Deploy Settings\", width: \"671px\", height: \"497px\" })\n}\n\n</script>\n<template>\n  <!-- <div>\n      <img class=\"logo\" src=\"https://www.onplateau.com/img/logo.svg\" />\n    </div> -->\n  <div class=\"contaier \">\n    <div class=\"row \" style=\"height:100%\">\n      <div  class=\"col-3 \">\n        <div class=\"vertical-nav-studio\" v-for=\"item in  viewModel!.studio!.ui?.navItems\" :key=\"item.name\">\n          <div @click=\"onNavItemSelect(item)\">\n            <i class=\"mdi mdi-menu mdi-24px menu-icon menu-icon-container\"></i>\n          </div>\n          <div>\n            <p class=\"explorer-text\">EXPLORER</p>\n          </div>\n          <div class=\"bottom-icons-container position-absolute bottom-0 p-1\">\n            <div>\n              <i class=\"mdi mdi-cog bottom-two bottom-icons\" @click=\"openSettings\"></i>\n            </div>\n          </div>\n        </div>\n      </div>\n      <div class=\"vertical-nav-expanded\">\n        <div class=\"expanded-menu-container\">\n          <div class=\"explorer-container\">\n            <p class=\"explorer-text-expanded\">EXPLORER</p>\n            <i class=\"mdi mdi-chevron-left chevron-left\" @click=\"onNavItemSelect(item)\"></i>\n          </div>\n          <div class=\"search-bar-container\">\n            <i class=\"mdi mdi-magnify search-icon\"></i>\n            <input type=\"search\" autocomplete=\"off\" placeholder=\"Search\">\n          </div>\n\n          <div class=\"chips-container\">\n            <button type=\"button\" class=\"chip chip1\"><i\n                class=\"mdi mdi-code-not-equal-variant chip-icons\"></i>Pages</button>\n            <button type=\"button\" class=\"chip chip2\"><i class=\"mdi mdi-database chip-icons\"></i>Domains</button>\n            <button type=\"button\" class=\"chip chip3\"><i class=\"mdi mdi-file-tree chip-icons\"></i>Processes</button>\n            <!-- <div class=\"up-down-icons\">\n              <i class=\"mdi mdi-chevron-down down-icon\"></i>\n              <i class=\"mdi mdi-chevron-up up-icon\"></i>\n            </div> -->\n          </div>\n\n          <!-- //app name and collapse button\n          <div class=\"treeview-header-container treeview-collapse-icon\">\n            <div class=\"col-11\">\n              <span style='padding-left: 27px;'>{{ viewModel.studio.appName }}</span>\n            </div>\n            <div class=\"col-1\">\n              <i class=\"mdi mdi-folder-multiple-outline collapse-icon\" style='cursor: pointer;  '\n                @click=\"collapse()\"></i>\n            </div>\n          </div> -->\n          <section v-show=\"ui?.sidebar.component\" class=\"sidebarSection\" ref=\"sidebarSectionElement\"\n            :style=\"{ width: leftpx }\">\n            <div class=\"splitter\" :style=\"{ left: leftpx }\" @mousedown=\"mousedown($event)\" @mouseup=\"mouseup\"></div>\n            <keep-alive>\n              <component :is=\"ui?.sidebar.component\"></component>\n            </keep-alive>\n          </section>\n\n          <div class=\"vertical-bottom-container position-absolute bottom-0\">\n            <div class=\"vertical-bottom-text\" style='cursor: pointer;' @click=\"addModule()\">\n              <p class=\"new-module-text\"><i class=\"mdi mdi-plus\"></i> Create New Module</p>\n            </div>\n                        <div class=\"vertical-bottom-text\" style='cursor: pointer;' @click=\"addScreen()\">\n              <p class=\"new-module-text\"><i class=\"mdi mdi-plus\"></i> Create New UI Screen</p>\n            </div>\n            <div class=\"vertical-bottom-text\" style='cursor: pointer;' @click=\"attachModuleToApp()\">\n              <p class=\"new-module-text\"><i class=\"mdi mdi-plus\"></i> Add Existing Module </p>\n            </div>\n\n            <div class=\"row bottom-row\">\n              <div class=\"col-12\">\n                <i class=\"mdi mdi-cog bottom-two icons-end\" @click=\"openSettings\"></i>\n              </div>\n            </div>\n            <!-- <div class=\"vertical-bottom-icons\">\n  